.popup {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.8);
  z-index: 999;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  visibility: hidden;
  overflow-x: auto;
  transition: 0.25s;
}
.popup .popup__wrapper {
  background: white;
  width: 720px;
  height: auto;
  padding: 56px 95px;
}
.popup .popup__wrapper .counter__wrapper .popup__heading {
  font-size: 2.625rem;
  margin-bottom: 1.5rem;
  font-weight: 700;
  margin-top: -12px;
}
.popup .popup__wrapper .counter__wrapper .popup__text {
  font-weight: 300;
}
.popup .popup__wrapper .counter__wrapper .popup__text .popup__counter {
  font-weight: 700;
}
.popup .popup__wrapper .counter__wrapper .btn__reset {
  background: #349499;
  border: none;
  padding: 1rem 2rem;
  border-radius: 2rem;
  color: white;
  cursor: pointer;
  font-weight: 700;
  transition: 0.25s ease-out;
  float: right;
  display: none;
  margin-right: -3.5rem;
  margin-bottom: -1rem;
}
.popup .popup__wrapper .counter__wrapper .btn__reset.visible {
  display: block;
}
.popup .popup__wrapper .counter__wrapper .btn__reset.clicked {
  background: transparent;
  color: #c20202;
  cursor: not-allowed;
  pointer-events: not-allowed;
}
.popup .popup__wrapper .counter__wrapper .btn__reset.clicked:hover {
  background: transparent;
}
.popup .popup__wrapper .counter__wrapper .btn__reset:hover {
  background: #E4C1AE;
  color: #121212;
}
.popup .popup__wrapper .counter__wrapper .btn__close {
  height: 18px;
  width: 18px;
  float: right;
  margin-right: -3.5rem;
  margin-top: -1rem;
  cursor: pointer;
}
.popup .popup__wrapper .table__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
}
.popup .popup__wrapper .table__wrapper .loader {
  width: 48px;
  height: 48px;
  animation: rotate;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}
.popup .popup__wrapper .table__wrapper .table {
  width: 100%;
  height: auto;
  background: rgb(255, 251, 243);
  font-size: 0.75rem;
}
.popup.active {
  opacity: 1;
  visibility: visible;
}

@keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
  }
}